.App {
    background: url(./img/bgcLap.png) no-repeat;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.content {
    width: 90%;
    height: 80%;
    margin: 25% 5%;
    opacity: 0.8;
    background-color: #000;
}

.header {
    position: absolute;
    top: 0;
    width: 80%;
    padding: 0 5%;
    height: 50px;
    background-color: #000;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

.downloadIcon {
    margin: 5px;
    cursor: pointer;
}

.buttons {
    display: flex;
}

.button {
    background-color: transparent;
    border: none;
    height: 50px;
    width: 40px;
    cursor: pointer;
}

.logoWrapper {
    width: 100%;
    display: flex;
    justify-content: right;
    margin-bottom: 40px;
}

.tabs {
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.btn {
    background-color: transparent;
    border: 0;
    color: #fff;
    width: 50%;
    height: 100%;
    padding: 20px 0;
    cursor: pointer;
    font-size: 20px;
}

.active {
    background-color: #fff;
    color: #000;
}

.logo {
    position: absolute;
    width: 200px;
    height: 140px;
    bottom: 400px;
    opacity: 0.3;
}

.title {
    color: #000;
    font-size: 20px;
    font-weight: 400;
    font-style: italic;
    padding-left: 10px;
    margin: 0;
    margin-bottom: 10px;
    background-color: #fff;
}

.menu {
    width: 90%;
    height: 80%;
    overflow: auto;
    margin: 0 5%;
    padding-right: 20px;
}

.menuItem {
    margin-bottom: 20px;
}

.list {
    list-style: none;
    margin: 0 20px;
    padding: 0;
}

.item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 700;
    color: #fff;
}

.ml {
    margin-right: 15px;
}
