@font-face {
  font-family: "Alegreya Sans";
  src: url(./fonts/AlegreyaSans-Black.ttf), url(./fonts//AlegreyaSans-BlackItalic.ttf);
}
@font-face {
  font-family: "Alegreya Sans";
  src: url(./fonts/AlegreyaSans-Bold.ttf), url(./fonts/AlegreyaSans-BoldItalic.ttf);
}
@font-face {
  font-family: "Alegreya Sans";
  src: url(./fonts/AlegreyaSans-ExtraBold.ttf) url(./fonts/AlegreyaSans-ExtraBoldItalic.ttf);
}
@font-face {
  font-family: "Alegreya Sans";
  src: url(./fonts/AlegreyaSans-Light.ttf) url(./fonts/AlegreyaSans-LightItalic.ttf);
}
@font-face {
  font-family: "Alegreya Sans";
  src: url(./fonts/AlegreyaSans-Medium.ttf) url(./fonts/AlegreyaSans-MediumItalic.ttf);
}
@font-face {
  font-family: "Alegreya Sans";
  src: url(./fonts/AlegreyaSans-Regular.ttf);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Alegreya Sans", sans-serif;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
